@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  color: $white;

  @media only screen and (max-width: $media-sm) {
    order: 3;

    * {
      font-family: 'Red Hat Display', sans-serif;
    }
  }
}

.content {
  max-width: $container;
  margin: 0 auto;
  padding: 85px 65px 55px;

  @media only screen and (max-width: $media-md) {
    padding: 70px 25px;
  }
}

.title {
  padding: 40px 10px 85px;
  text-align: center;

  @media only screen and (max-width: $media-md) {
    display: none;
  }
}

.titleMobile {
  display: none;

  @media only screen and (max-width: $media-md) {
    display: block;
    letter-spacing: -1.54px;
    text-align: center;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media only screen and (max-width: $media-md) {
    margin-top: 60px;
    justify-content: center;
  }
}

.item {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 400px;
  width: 100%;
  min-height: 200px;
  margin-bottom: 100px;

  @media only screen and (max-width: $media-sm) {
    flex-direction: column;
    margin-bottom: 50px;
  }
}

.itemContetnt{
  color: $gray;
  max-width: 325px;
}

.itemIcon {
  margin-right: 30px;
}

.itemTitle {
  margin-bottom: 15px;
  font-size: 23px;
  font-weight: 700;
  color: $grayDark;

  @media only screen and (max-width: $media-sm) {
    color: $black;
    margin-top: 20px;
    font-size: 21px;
  }
}

.itemText {
  font-size: 14px;
  line-height: 27px;

  @media only screen and (max-width: $media-sm) {
    font-size: 16px;
    line-height: 30px;
    color: $black;
  }
}