@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  background: $gray_bg;

  @media only screen and (max-width: $media-sm) {
    order: 6;

    &:not(.platformWrap) {
      * {
        font-family: 'Red Hat Display', sans-serif;
      }
    }
  }
}

.content {
  max-width: $container;
  margin: 0 auto;
  padding: 100px 90px 115px;

  @media only screen and (max-width: $media-md) {
    padding: 65px 0 50px 0;
  }
}

.title {
  padding: 15px 10px;

  @media only screen and (max-width: $media-sm) {
    letter-spacing: -1.5px;
  }
}

.tabsWrap {
  display: flex;
  margin-top: 75px;

  @media only screen and (max-width: $media-md) {
    margin-top: 35px;
  }
}

.tabs {
  width: 50%;
  padding: 60px 60px 60px 0;
  border-right: 1px solid #707070;

  @media only screen and (max-width: $media-md) {
    width: 100%;
    border: 0;
    padding: 0;
  }
}

.tabsBtn {
  width: 100%;
  padding: 10px 45px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: none;
  background: rgba($color: $white, $alpha: 0.2);
  text-align: left;
  color: $grayDark;
  font-size: 21px;
  font-weight: 500;
  cursor: pointer;
  transition: .3s;  

  @media only screen and (min-width: $media-md + 1) {
    &:hover {
      background: $dark;
      color: $white;
    }
  }

  @media only screen and (max-width: $media-md) {
    max-width: 100%;
  }

  @media only screen and (max-width: $media-sm) {
    font-size: 13px;
    border-radius: 0;
    margin-bottom: 10px;
    padding: 5px 40px;
  }
}

.activeTab {
  background: $green;
  color: $white;
  font-size: 23px;
  font-weight: 700;
  
  @media only screen and (max-width: $media-sm) {
    font-size: 14px;
  }
}

.tabContent {
  width: 50%;
  padding: 60px 0 60px 60px;
  opacity: 0;
  visibility: hidden;
  transition: .5s;
  
  @media only screen and (max-width: $media-md) {
    display: none;
  }
}

.tabContentShow {
  visibility: visible;
  opacity: 1;
}

.tabTitle {
  font-size: 30px;
  font-weight: 700;
  color: $grayDark;
  margin-bottom: 30px;
}

.tabText {
  font-size: 14px;
  line-height: 30px;
  color: $gray;
  margin-bottom: 30px;

  @media only screen and (max-width: $media-md) {
    margin-bottom: 30px;
    line-height: 20px;
  }
}

.platformWrap {
  display: flex;
  flex-wrap: wrap;
  padding-left: 15px;

  @media only screen and (max-width: $media-md) {
    flex-direction: column;
  }
}

.platformItem {
  position: relative;
  width: 50%;
  color: $grayDark;
  margin-bottom: 10px;
  padding-right: 30px;
  font-weight: 500;

  &:before {
    content: '.';
    position: absolute;
    left: -15px;
    top: -3px;
  }

  @media only screen and (max-width: $media-sm) {      
    width: 100%;
  }
}

.tabContentMobile {
  height: 0;
  box-sizing: border-box;
  padding: 0 50px;
  opacity: 0;
  overflow: hidden;
}

.showContentMobile {
  
  @media only screen and (max-width: $media-md) {      
    height: 100%;
    opacity: 1;
    padding: 10px 25px;
    margin-bottom: 30px;
    animation: show 0.5s;
  }
}

@keyframes show {
  0% {
    margin-top: 30%;
  }

  100% {
    margin-top: 0;
  }
}