@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  background: $grayDark;

  @media only screen and (max-width: $media-sm) {
    order: 4;
  }
}

.content {
  max-width: $container;
  margin: 0 auto;
  padding: 85px 160px 60px;
  color: $white;

  @media only screen and (max-width: $media-xl) {
    padding: 80px 100px;
  }

  @media only screen and (max-width: $media-md) {
    padding: 70px 10px;
  }
}

.title {
  color: $white;
  padding: 0 10px 60px;
  text-align: center;
  font-family: 'Red Hat Display', sans-serif;

  @media only screen and (max-width: $media-md) {
    padding: 0 0 75px 0;
    font-family: 'Montserrat', sans-serif;
  }
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.item {
  width: 295px;
  min-height: 200px;
  margin-bottom: 30px;

  @media only screen and (max-width: $media-md) {
    width: 50%;
    padding: 0 15px;
    min-height: auto;
  }

  @media only screen and (max-width: $media-sm) {
    width: 100%;
    margin-bottom: 50px;
  }
}

.itemCount {
  font-size: 40px;
  font-weight: 700;
  color: $white;
  letter-spacing: -1.54px;
}

.itemTitle {
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
  color: $green;

  @media only screen and (max-width: $media-sm) {
    letter-spacing: -0.77px;
    font-weight: 500;
  }
}

.itemText {
  font-size: 14px;
  line-height: 25px;

  @media only screen and (max-width: $media-sm) {
    letter-spacing: -0.62px;
    line-height: 30px;
    font-size: 16px;
  }
}