@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {  
  background: $gray_bg;

  @media only screen and (max-width: $media-sm) {
    order: 9;
  }
}

.content {
  max-width: $container;
  min-height: 340px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;

  @media only screen and (max-width: $media-sm) {
    min-height: 200px;
  }
}

.imgWrap {
  height: 100px;
  display: flex;
  align-items: center;
}

.img {
  margin: 0 auto;
}