@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  position: relative;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: $dark;
  color: $white;

  @media only screen and (max-width: $media-sm) {
    order: 1;
  }
}
.slide {
  max-width: $container;
  width: 100%;
  margin: 0 auto;
  padding: 295px 150px;
  outline: none;
  min-height: 1110px;

  @media only screen and (max-width: $media-xl) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 150px 100px;
  }

  @media only screen and (max-width: $media-md) {
    padding: 140px 50px;
    min-height: 825px;
  }

  @media only screen and (max-width: $media-sm) {
    padding: 140px 25px 20px;
    display: block;
  }
}

.title {
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 35px;

  @media only screen and (max-width: $media-md) {
    text-align: center;
  }

  @media only screen and (max-width: $media-md - 1) {
    font-size: 40px;
  }
}

.description {
  font-size: 14px;
  line-height: 33px;

  @media only screen and (max-width: $media-sm) {
    line-height: 25px;
  }
}

.descriptionListWrap {
  margin-left: 20px;
}

.descriptionList {
  font-size: 13px;
  line-height: 33px;
  color: $white;

  @media only screen and (max-width: $media-sm) {
    line-height: 25px;
  }
}

.bottomWrap {
  position: absolute;
  bottom: 6%;
}

.arrowWrap {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.arrow {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 27px;
  height: 27px;
  border: 1px solid $green;
  border-radius: 50%;
  margin-bottom: 12px;

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid $green;
  }
}

.arrowDown {
  position: relative;
  width: 1px;
  height: 10px;
  display: block;
  transform: translateY(-1px);
  background: rgba($color: $white, $alpha: .8);

  &:after {
    position: absolute;
    content: '';
    display: block;
    top: 100%;
    left: -2px;
    width: 0;
    height: 0;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    border-top: 4px solid rgba($color: $white, $alpha: .8);
  }
}

.arrowText {
  font-family: 'Red Hat Display', sans-serif;
  font-size: 15px;
  font-weight: 500;
  letter-spacing: -0.58px;
}

.link {
  display: none;
  margin-bottom: 35px;
  line-height: 18px;
  text-decoration: none;
  color: $white;
  background: $green;
  padding: 9px 30px;
  font-size: 14px;
  font-weight: 700;
  border-radius: 5px;
  font-family: 'Red Hat Display', sans-serif;

  @media only screen and (max-width: $media-sm) {
    display: flex;
  }
}

:global {

  .slick-slide {
    div {
      outline: none;
    }
  }

  .slick-dots {
    bottom: 250px;

    @media only screen and (max-width: $media-xl) {
      bottom: 17%;
    }

    @media only screen and (max-width: $media-sm) {
      bottom: 20%;
    }

    li button:before {
      font-size: 10px;
      color: rgba($color: $white, $alpha: 0.7);
    }

    li.slick-active button:before {
      opacity: 1;
      color: $green;
    }
  }

  .slick-prev,
  .slick-next {
    width: 32px;
    height: 32px;
    z-index: 10;

    &:before {
      content: '';
      display: flex;
      width: 100%;
      height: 100%;
      background: url('../../images/icons/right-arrow.png') no-repeat;
    }
  }

  .slick-prev {
    left: 25px;

    &:before {
      transform: rotate(180deg);
    }
  }

  .slick-next {
    right: 25px;
  }
}