@import '../../styles/variables';

footer {
  background: $dark;
  min-height: 62px;
  color: $white;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  font-size: 11px;
  padding: 0 125px;

  @media only screen and (max-width: $media-md) {
    padding: 0 60px;
  }

  @media only screen and (max-width: $media-sm) {
    padding: 0 60px 20px 60px;
  }
}

.logoWrap {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: $white;
  cursor: pointer;
  background: none;
  border: none;

  &:focus {
    outline: none;
  }

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.logoTitle {
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}

.burger {
  position: relative;
  display: flex;
  width: 16px;
  height: 4px;
  margin-right: 3px;
  background: $white;

  &:before,
  &:after {
    content: '';
    position: absolute;
    left: 0;
    display: flex;
    width: 16px;
    height: 4px;
    background: $white;
  }

  &:before {
    top: -7px;
  }

  &:after {
    top: 7px;
  }
}


.socialWrap {
  display: flex;
  align-items: center;

  @media only screen and (max-width: $media-sm) {
   width: 100%;
   flex-direction: column;
  }
}

.listWrap {
  display: flex;
  list-style: none;

  @media only screen and (max-width: $media-sm) {
    width: 100%;
    margin: 30px 0;
    justify-content: space-between;
  }
}

.item {
  margin-right: 15px;

  &:last-child {
    margin-right: 50px;

    @media only screen and (max-width: $media-sm) {
      margin-right: 0;
    }
  }
}

.link {
  @media only screen and (max-width: $media-sm) {
    display: flex;
    width: 20px;
    height: 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }
}

