@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  min-height: 550px;
  padding: 100px 20px 40px;
  color: $white;
  background: url('../../images/bg_mission.jpg') no-repeat;  
  background-size: cover;

  @media only screen and (max-width: $media-sm) {
    order: 7;
    padding: 30px 25px;
  }
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;

  @media only screen and (max-width: $media-sm) {
    padding-bottom: 60px;
  }
}

.title {
  padding: 25px 10px;
  color: $white;
}

.titleTwo {
  padding: 60px 10px;
  font-family: 'Red Hat Display', sans-serif;

  @media only screen and (max-width: $media-sm) {
    padding: 45px 0 35px;
  }
}

.description {
  text-align: center;
  font-size: 22px;
  line-height: 40px;
  letter-spacing: 0;

  @media only screen and (max-width: $media-sm) {
    font-size: 16px;
    text-align: left;
    line-height: 30px;
  }
}

.clientsWrap {
  padding: 65px 0 120px;
  background: $gray_bg;

  @media only screen and (max-width: $media-sm) {
    background: none;
    padding: 0;
    order: 5;
  }
}

.logoWrap {
  @supports (display: grid) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    align-items: center;
    justify-items: center;
  }
  
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0 20px;
  

  @media only screen and (max-width: $media-xl) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    padding: 0 45px;
  }

  @media only screen and (max-width: $media-md) {
    grid-template-columns: 1fr 1fr 1fr;
  }

  @media only screen and (max-width: $media-sm) {
    grid-template-columns: 1fr 1fr;
    padding: 0;
  }
}

.logoImg {
  margin: 27px auto;
  flex-basis: 200px;
  opacity: 0.7;
  transition: 0.2s;
  
  &:hover {
    opacity: 1;
  }
}