@import '../../styles/variables';
@import '../../styles/repeat';

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance:textfield;
}

textarea {
	-ms-overflow-style: none;
}

.wrapper {
  background: $green;

  @media only screen and (max-width: $media-sm) {
    order: 10;
  }
}

.content {
  max-width: $container;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 115px 110px 60px;
  color: $white;
  font-size: 24px;

  @media only screen and (max-width: $media-md) {
    padding: 75px 20px 45px;    
    font-size: 18px;
    flex-direction: column;
  }
}

.contactsWrap {
  width: 50%;
  margin-right: 20px;

  @media only screen and (max-width: $media-md) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    padding: 0;
    padding: 0 10px;
  }
}

.title {
  color: $white;
  text-align: left;
  font-family: 'Montserrat', sans-serif;

  @media only screen and (max-width: $media-md) {
    text-align: center;
    width: 100%;
  }
}

.address {
  margin-top: 55px;

  @media only screen and (max-width: $media-md) {
    margin-top: 45px;
    margin-right: 15px;
  }

  @media only screen and (max-width: $media-sm) {
    margin-right: 0;
  }
}

.street {
  font-weight: 600;
  margin-bottom: 5px;
}

.contacts {
  margin-top: 50px;

  a {
    text-decoration: none;
    color: $white;
    display: block;
    margin-bottom: 5px;
  }
}

.contactFormWrap {
  width: 50%;
  display: flex;
  flex-direction: column;

  @media only screen and (max-width: $media-md) {
    width: 100%;
    margin-top: 50px;
  }
}

.formLine {
  width: 100%;
  display: flex;
  justify-content: space-between;

  @media only screen and (max-width: $media-md) {
    flex-direction: column;
  }
}

.formGroup {
  height: 54px;
  position: relative;
  width: 48%;
  margin-bottom: 30px;

  @media only screen and (max-width: $media-md) {
    width: 100%;
  }
}

.input {
  position: relative;
  width: 100%;
  height: 100%;
  border: 0;
  background: rgba($color:$white, $alpha: 0.25);
  color: $white;
  border-radius: 4px;
  padding: 18px 15px;
}

.label {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  color: $white;
  background: none;
  font-size: 16px;
  padding: 18px 15px;
  transition: 0.2s;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @media only screen and (max-width: $media-md) {
    font-size: 14px;
    color: rgba($color: $white, $alpha: 0.7);
  }
}

.labelFocus {
  transition: 0.2s;
  transform: translateY(-40px);
}

.formArea {
  position: relative;
  margin-bottom: 25px;

  textarea {
    height: 150px;
  }
}

.agreeWrap {
  font-size: 12px;
  display: flex;

  input {
    margin-right: 20px;
  }

  @media only screen and (max-width: $media-md) {
    color: #F9F9F9;
  }
}

.buttonsWrap {
  margin-top: 30px;
}

.send {
  display: flex;
  margin-left: auto;
  background: $white;
  border-radius: 5px;
  color: $green;
  padding: 10px 50px;
  border: 0;
  font-size: 14px;
  font-weight: 700;
  border: 1px solid $white;
  cursor: pointer;

  &:disabled {
    border: 1px solid #999999;
    background-color: #cccccc;
    color: #666666;
    cursor: not-allowed;
  }

  @media only screen and (max-width: $media-md) {
    margin: 0 auto;
  }
}

.response {
  font-size: 18px;
  background: rgba($color:$white, $alpha: 0.25);
  border-radius: 5px;
  margin-top: 15px;

  span {
    display: flex;
    padding: 20px;
  }
}

.responseError {
  color: red;
}

.checkbox {
  position: absolute;
  opacity: 0;

  & + label {
    position: relative;
    cursor: pointer;
    padding: 0;
  }

  & + label:before {
    content: '';
    margin-right: 10px;
    display: inline-block;
    vertical-align: text-top;
    width: 20px;
    height: 20px;
    background: none;
    border: 2px solid  $white;
    border-radius: 2px;
    box-sizing: border-box;
  }
  
  &:focus + label:before {
    box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
  }
  
  &:disabled + label {
    color: #b8b8b8;
    cursor: auto;
  }

  &:disabled + label:before {
    box-shadow: none;
    background: #ddd;
  }

  &:checked + label:after {
    content: '';
    position: absolute;
    left: 5px;
    top: 9px;
    background: white;
    width: 2px;
    height: 2px;
    box-shadow: 
      2px 0 0 white,
      4px 0 0 white,
      4px -2px 0 white,
      4px -4px 0 white,
      4px -6px 0 white,
      4px -8px 0 white;
    transform: rotate(45deg);
  }
}