@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  background: $gray_bg;

  @media only screen and (max-width: $media-sm) {
    order: 2;
  }
}

.content {
  max-width: $container;
  margin: 0 auto;
  padding: 135px 190px 110px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media only screen and (max-width: $media-xl) {
    padding: 80px;
  }

  @media only screen and (max-width: $media-md) {
    padding: 75px 0 45px;
  }
}

.contentTop {
  width: 100%;

  @media only screen and (max-width: $media-md) {
    padding: 0 40px;
    line-height: 30px;
    font-size: 16px;
    text-align: left;
  }
}

.title {
  margin: 0;
}

.subTitle {
  margin: 20px auto 50px;
  color: $gray;
  font-weight: 500;
  text-align: center;

  @media only screen and (max-width: $media-md) {
    margin: 35px auto;
    font-weight: 400;
    text-align: left;
  }
}

.description {
  color: $gray;
  text-align: center;
  line-height: 30px;
  font-size: 14px;

  @media only screen and (max-width: $media-md) {
    text-align: left;
    font-size: 16px;
    line-height: 30px;
  }
}

.list {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 70px;

  @media only screen and (max-width: $media-xl) {
    justify-content: center;
  }

  @media only screen and (max-width: $media-sm) {
    background: $white;
    padding: 0;
  }
}

.item {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  width: 320px;
  padding: 20px;
  min-height: 390px;
  background: $white;
  margin: 0 10px 55px 10px;

  @media only screen and (max-width: $media-xl) {
    min-height: auto;
  }

  @media only screen and (max-width: $media-sm) {
    width: 100%;
    justify-content: flex-start;
    margin: 0 0 35px 0;
    padding: 35px 25px 20px;
  }
}

.itemActive {
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.3);
  padding-bottom: 40px;
}

.contentWrap {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.itemTitle {
  color: $grayDark;
  font-size: 18px;
  font-weight: bold;
  margin: 15px 0 40px;

  @media only screen and (max-width: $media-sm) {
    margin: 15px 0 0 0;
    letter-spacing: -0.69px;
  }
}

.itemButton {
  border: 1px solid $green;
  border-radius: 5px;
  background: $white;
  padding: 5px 30px;
  color: $green;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: .3s;

  &:hover {
    background: $green;
    color: $white;
  }

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.itemButtonHidden {
  visibility: hidden;
}

.modal {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: $white;
  opacity: 0;
  visibility: hidden;
  transition: .5s;
}

.showModal {
  visibility: visible;
  opacity: 1;

  @media only screen and (max-width: $media-md) {
    margin: 0 15px;
  }

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.close {
  position: absolute;
  top: 60px;
  right: 50px;
  width: 44px;
  height: 44px;
  opacity: 0.3;
  cursor: pointer;  
  border: none;
  background: none;

  &:hover {
    opacity: 1;
  }

  &:before, &:after {
    position: absolute;
    content: '';
    height: 16px;
    width: 2px;
    background-color: $gray;
    top: 16px;
    left: 21px;
    transform: translate(-50%, -50%);
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
}

.modalItemWrap {
  overflow: auto;

  * {
    font-family: 'Red Hat Display', sans-serif;
  }

  .servicesWrap {
    font-size: 12px;
  }
}

.modalItem {
  padding: 60px;
  font-size: 14px;
  color: $gray;
}

.modalTitle {
  color: $dark;
  margin: 15px 0 30px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: -0.69px;
}

.textItem {
  margin-bottom: 20px;

  @media only screen and (max-width: $media-sm) {
    font-size: 14px;
    line-height: 25px;
    color: $gray;
  }
}

.servicesWrap {
  margin-left: 15px;
  list-style-type: none;

  @media only screen and (max-width: $media-sm) {
    font-size: 14px;
    line-height: 25px;
    color: $gray;
  }
}

.servicesItem {
  margin-bottom: 5px;

  &:before {
    content: "\2022";
    color: $green;
    font-weight: bold;
    display: inline-block;
    width: 1em;
    margin-left: -1em;
  }
}

.itemButtonMobile {
  min-width: 44px;
  width: 44px;
  height: 44px;
  display: none;
  background: none;
  border: none;
  transition: 0.3s;

  img {
    width: 11px;
    height: 20px;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }

  @media only screen and (max-width: $media-sm) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.itemButtonActive {
  img {
    transform: rotate(90deg);
  }
}

.modalItemMobile {
  width: 100%;
  height: 0;
  box-sizing: border-box;
  padding-top: 20px;
  opacity: 0;
  overflow: hidden;

  * {
    font-family: 'Red Hat Display', sans-serif;
  }
}

.showItemMobile {
  height: 100%;
  opacity: 1;
  transition: .3s;
}