@import '../../styles/variables';

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  padding: 0 100px;
  justify-content: space-between;
  align-items: center;
  color: $white;
  height: $header;
  transition: 0.5s;
  z-index: 5;
  
  @media only screen and (max-width: $media-xl) {
    padding: 0 40px;
  }

  @media only screen and (max-width: $media-esm) {
    padding: 0 20px;
    height: 60px;
  }
}

.headerBg {
  background: $dark;
  box-shadow: 0 4px 8px 0 rgba(60,61,65,.2);
  height: 60px;
}
.logoWrap {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  background: none;
  border: none;
  cursor: pointer;
  margin-right: 20px;

  &:focus {
    outline: none;
  }
}

.nav {
  @media only screen and (max-width: $media-md) {
    position: absolute;
    width: 0;
    top: 0;
    right: -100%;
    transition: 0.3s;
  }
}

.navMobile {
  width: 100vw;
  height: 100vh;
  right: 0;
}

.listWrap {
  height: 35px;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  transition: 0.5s;

  @media only screen and (max-width: $media-md) {
    width: 100%;
    height: 100vh;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    background: $dark;
    padding: 50px;
    margin: 0;
  }
}

.item {
  display: flex;
  align-items: center;
  transition: .3s;

  &:hover {
    transform: translateY(-10px);

    @media only screen and (max-width: $media-md) {
      transform: none;
    }
  }
  
  &:not(:last-child) {
    margin-right: 40px;

    @media only screen and (max-width: $media-xl) {
      margin-right: 20px;
    }

    @media only screen and (max-width: $media-md) {
      margin: 0 0 25px 0;
      font-size: 24px;
    }
  }

  &:last-child {

    .link {
      background: $green;
      padding: 0 30px;
      border-radius: 5px;

      @media only screen and (max-width: $media-md) {
        padding: 10px;
      }
    }
  }  

  @media only screen and (max-width: $media-md) {
    width: 100%;
    text-align: center;
    font-size: 24px;

    a {
      width: 100%;
    }
  }
}

.link {
  line-height: 35px;
  text-decoration: none;
  color: $white;
}

.iconBurder {
  position: relative;
  display: none;
  width: 30px;
  height: 18px;
  transform: rotate(0deg);
  background: none;
  border: none;
  outline: none;
  z-index: 10;
  transition: .5s ease-in-out;
  cursor: pointer;

  span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    background: $white;
    border-radius: 4px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;

    &:nth-child(1) {
      top: 0px;
      transform-origin: left center;
    }

    &:nth-child(2) {
      top: 7px;
      transform-origin: left center;
    }

    &:nth-child(3) {
      top: 14px;
      transform-origin: left center;
    }
  }  

  @media only screen and (max-width: $media-md) {
    display: block;
  }
}

.iconBurderOpen {
  span {
    &:nth-child(1) {
      transform: rotate(45deg);
      top: -3px;
      left: 6px;
    }

    &:nth-child(2) {
      width: 0%;
      opacity: 0;
    }

    &:nth-child(3) {
      transform: rotate(-45deg);
      top: 18px;
      left: 6px;
    }
  }
}