@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  max-width: $container;
  min-height: 700px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;

  @media only screen and (max-width: $media-md) {
    flex-direction: column;
    min-height: auto;
  }

  @media only screen and (max-width: $media-sm) {
    order: 1;
  }
}

.bg {
  width: 50%;
  min-height: inherit;
  background: url('../../images/company_map.svg') no-repeat;
  background-position-x: -70px;
  background-size: cover;

  @media only screen and (max-width: $media-md) {
    width: 100%;
    min-height: auto;
    height: 260px;
    background-position: center;
  }
}

.content {
  width: 50%;
  padding: 70px 50px 70px 100px;

  @media only screen and (max-width: $media-xl) {
    padding: 50px;
  }

  @media only screen and (max-width: $media-md) {
    width: 100%;
    padding: 75px 25px 0;
  }
}

.title {
  margin-bottom: 60px;
  text-align: left;

  @media only screen and (max-width: $media-md) {
    margin-bottom: 30px;
    text-align: center;
  }
}

.item {
  color: $gray;
  padding-bottom: 50px;
  font-size: 14px;

  @media only screen and (max-width: $media-md) {
    padding-bottom: 15px;
    font-size: 16px;
    line-height: 30px;
    color: $gray;
  }
}