@import '../../styles/variables';
@import '../../styles/repeat';

.wrapper {
  max-width: $container;
  width: 100%;
  margin: 0 auto;
  padding: 120px 70px 90px;

  @media only screen and (max-width: $media-md) {
    padding: 60px 20px;
  }

  @media only screen and (max-width: $media-sm) {
    padding: 60px 0 80px;
    order: 8;
  }
}

.title {
  @media only screen and (max-width: $media-sm) {
    font-family: 'Red Hat Display', sans-serif;
  }
}

.cards {
  margin-top: 70px;

  @media only screen and (max-width: $media-sm) {
    background: $gray_bg;
  }
}

.card {
  position: relative;
  display: flex;
  min-height: 390px;
  margin-bottom: 45px;
  color: $grayDark;
  overflow: hidden;
  
  &:nth-child(even) {
    .cardContent {
      order: 1;

      @media only screen and (max-width: $media-sm) {
        order: 0;
      }
    }

    .descriptionCard {
      margin-left: 100%;

      @media only screen and (max-width: $media-sm) {
        margin: 0;
      }
    }
  }

  &:nth-child(odd) {
    .descriptionCard {
      margin-left: -100%;

      @media only screen and (max-width: $media-sm) {
        margin: 0;
      }
    }
  }

  @media only screen and (max-width: $media-sm) {
    margin-bottom: 55px;
    flex-direction: column;

    &:last-child {
      .cardContent,
      .descriptionCardActive {
        background: $white;
      }
    }
  }
}

.cardInner {
  position: relative;

  @media only screen and (max-width: $media-sm) {
    max-width: 80%;
    width: 100%;
    margin: 0 auto;
    margin-top: -50px;
    background: $white;
    padding: 15px 10px;
  }

  @media only screen and (max-width: $media-esm) {
    max-width: 280px;
  }
}

.cardContent {
  width: 45%;
  padding: 40px 40px 30px 70px;
  background: $gray_bg;

  @media only screen and (max-width: $media-xl) {
    padding: 40px;
    width: 60%;
  }

  @media only screen and (max-width: $media-sm) {
    width: 100%;
    padding: 0;
  }
}

.cardContentHidden {

  @media only screen and (min-width: $media-sm) {
    display: none;
  }
}

.mobileImg {
  display: none;

  @media only screen and (max-width: $media-sm) {
    display: flex;
    height: 320px;
    width: 100%;
    object-fit: cover;
  }
}

.cardTitle {
  font-size: 52px;
  font-weight: 700;
  color: $grayDark;
  font-family: 'Red Hat Display', sans-serif;

  @media only screen and (max-width: $media-xl) {
    font-size: 40px;
    padding-right: 45px;
  } 

  @media only screen and (max-width: $media-md) {
    font-size: 24px;
    padding-right: 45px;
  }  
}

.cardTitleModal {
  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.categories {
  margin: 20px 0 35px;

  @media only screen and (max-width: $media-md) {
    margin: 20px 0 30px;
  }
}

.category {
  display: inline-flex;
  padding: 5px 15px;
  margin-right: 10px;
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: 500;
  border-radius: 5px;
  background: $green;
  color: $white;  

  @media only screen and (max-width: $media-sm) {
    font-family: 'Red Hat Display', sans-serif;
    font-size: 9px;
    margin-right: 5px;
  }
}

.cardText {
  line-height: 32px;

  @media only screen and (max-width: $media-md) {
    font-size: 12px;
    line-height: 20px;
    font-weight: 500;
  }
}

.cardBtn {
  margin-top: 50px;
  border: 1px solid $green;
  border-radius: 5px;
  background: $white;
  padding: 5px 30px;
  color: $green;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: .3s;

  &:hover {
    background: $green;
    color: $white;
  }
}

.cardBtnHidden {
  visibility: hidden;
}

.showDescription {
  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.img {
  width: 55%;

  @media only screen and (max-width: $media-xl) {
    width: 40%;
  }

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.descriptionCard {
  width: 0;
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  background: $white;
  transition: 0.5s;
  overflow: auto;

  ul {
    margin-left: 2em;
    font-size: 14px;
  
    li {
      margin-bottom: 5px;
      color: #656B6F;
      line-height: 30px;
    }
  }

  @media only screen and (max-width: $media-md - 1) {
    flex-direction: column;
  }

  @media only screen and (max-width: $media-sm) {
    width: 100%;
    position: relative;
    height: 0;
    box-sizing: border-box;
    padding: 0 50px;
    opacity: 0;
    overflow: hidden;
    transition: .2s;
  }
}

.descriptionCardActive {
  margin: 0!important;
  position: relative;
  padding: 45px;
  min-width: 100%;
  width: 100%;
  border: 25px solid $green;

  @media only screen and (max-width: $media-sm) {
    background: $gray_bg;
    border: none;
    height: 100%;
    opacity: 1;
    padding: 0;

    * {
      font-size: 12px;
      line-height: 20px;
    }
  }
}

.colWrap {
  width: 50%;

  &:first-child {
    padding-right: 45px;
  }

  @media only screen and (max-width: $media-md - 1) {
    width: 100%;
  }

  @media only screen and (max-width: $media-sm) {
    min-width: 80%;
    width: 80%;
    margin: 0 auto;
    padding: 0 10px;
    background: $white;

    &:first-child {
      padding: 0 10px;
    }
  }

  @media only screen and (max-width: $media-esm) {
    max-width: 280px;
    width: 100%;
  }
}

.descriptionItem {
  font-size: 14px;
  color: $grayDark;
  line-height: 32px;
  margin-bottom: 15px;

  @media only screen and (max-width: $media-sm) {
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 20px;
  }
}

.descriptionItemRight {
  line-height: 30px;
  color: $gray;

  @media only screen and (max-width: $media-sm) {
    line-height: 20px;
  }
}

h4 {
  margin: 10px 0 5px;
}

.close {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 44px;
  height: 44px;
  opacity: 0.3;
  cursor: pointer;
  border: none;
  background: none;

  &:hover {
    opacity: 1;
  }

  &:before, &:after {
    position: absolute;
    content: '';
    height: 16px;
    width: 2px;
    background-color: #707070;
    top: 14px;
    left: 21px;
    transform: translate(-50%, -50%);
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }

  @media only screen and (max-width: $media-sm) {
    display: none;
  }
}

.showMore {
  display: flex;
  margin: 125px auto 0;

  @media only screen and (max-width: $media-sm) {
    margin-top: 60px;
  }
}

.itemButtonMobile {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  min-width: 44px;
  width: 44px;
  height: 44px;
  background: none;
  border: none;
  transition: 0.3s;

  img {
    width: 11px;
    height: 20px;
  }

  @media only screen and (max-width: $media-sm) {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
  }
}

.itemButtonActive {
  transform: rotate(90deg);
}
